import styled from '@emotion/styled';
import * as React from 'react';

type Props = {
  color?: string;
  disabled?: boolean;
  next?: boolean;
  down?: boolean;
  up?: boolean;
};

export const TimerArrowIcon: React.FC<Props> = props => (
  <Root
    {...props}
    xmlns='http://www.w3.org/2000/svg'
    width='5'
    height='8'
    viewBox='0 0 5 8'
  >
    <path d='M2.414 4l2.293 2.293a1 1 0 0 1-1.414 1.414l-3-3a1 1 0 0 1 0-1.414l3-3a1 1 0 1 1 1.414 1.414L2.414 4z' />
  </Root>
);

const Root = styled.svg<Props>`
  transform: rotate(${getRotation}deg);

  path {
    fill: ${props =>
      props.disabled
        ? props.theme.colors.ui_60.regular
        : props.color || props.theme.colors.ui_40.regular};
    fill-rule: nonzero;
  }

  ${props =>
    !props.disabled &&
    `
  &:hover {
    path {
      fill: ${props.theme.colors.ui_40.regular};
    }
  }`};
`;

function getRotation(props: Props) {
  if (props.next) return 180;
  if (props.down) return -90;
  if (props.up) return 90;
  return 0;
}
