import * as React from 'react';
import { css } from '@emotion/react';
import styled from '@emotion/styled';
import * as Browser from 'webextension-polyfill';

import { PrimaryButton, Link } from '@ui/components/index';
import Logo from '@ui/icons/Logo';
import { defineMessages, FormattedMessage } from 'react-intl';
import flower from './assets/flower.png';
import flower2x from './assets/flower@2x.png';
import flower3x from './assets/flower@3x.png';

const messages = defineMessages({
  heading: {
    id: 'login.heading',
    defaultMessage: 'Nearly there!',
  },
  subheading: {
    id: 'login.subheading',
    defaultMessage: 'Click the button below to login to your Toggl account',
  },
  loginButton: {
    id: 'login.loginButton',
    defaultMessage: 'Log in',
  },
  createAccount: {
    id: 'login.createAccount',
    defaultMessage: 'Sign up instead',
  },
  loading: {
    id: 'login.loggingIn',
    defaultMessage: 'Logging in to Toggl Track Browser Extension',
  },
  error: {
    id: 'login.error',
    defaultMessage: 'Could not login to Toggl Track Browser Extension',
  },
  loggedIn: {
    id: 'login.loggedIn',
    defaultMessage: 'Welcome to Toggl Track Browser Extension',
  },
});

type Props = {
  hideSignup?: boolean;
  hideHeading?: boolean;
  customSubheading?: React.ReactNode;
};

export const LoginPage: React.FC<Props> = props => {
  return (
    <>
      <Root>
        <Logo height='38px' width='157px' />
        <Image
          src={flower}
          srcSet={`${flower2x} 2x, ${flower3x} 3x`}
          alt='Toggl Track login required'
          css={css`
            ${props.hideHeading && 'padding-bottom: 0;'}
          `}
        />
        {!props.hideHeading && (
          <Heading>
            <FormattedMessage {...messages.heading} />
          </Heading>
        )}
        <div>
          {props.customSubheading ? (
            props.customSubheading
          ) : (
            <FormattedMessage {...messages.subheading} />
          )}
        </div>
        <LoginButton />
        {!props.hideSignup && <SignupButton />}
      </Root>
    </>
  );
};

const LoginButton = () => {
  const url = `${process.env.TOGGL_ACCOUNTS_HOST}/login/`;
  return (
    <a href='#' onClick={openPage(url)}>
      <PrimaryButton
        css={css`
          border-radius: 32px;
          width: 200px;
          height: 36px;
        `}
      >
        <FormattedMessage {...messages.loginButton} />
      </PrimaryButton>
    </a>
  );
};

const SignupButton = () => {
  const url = `${process.env.TOGGL_ACCOUNTS_HOST}/signup/?utm_source=toggl-button&utm_medium=referral`;
  return (
    <SignupLink href='#' onClick={openPage(url)}>
      <FormattedMessage {...messages.createAccount} />
    </SignupLink>
  );
};

const openPage = (url: string) => () => Browser.tabs.create({ url });

const Root = styled.div`
  min-height: 40vh;
  display: flex;
  flex-direction: column;
  gap: 16px;
  align-items: center;
  padding: 50px 40px;
  background-color: ${({ theme }) => theme.colors.background.regular};
  color: ${({ theme }) => theme.colors.ui.regular};
  font-family: ${({ theme }) => theme.font.roboto};
  font-size: 14px;
  font-weight: 400;
`;

const Heading = styled.h3`
  margin: 0;
  font-size: 20px;
  font-weight: ${({ theme }) => theme.text.semibold};
  line-height: 1.6;
`;

const Image = styled.img`
  padding: 10px 0;
`;

const SignupLink = styled(Link)`
  font-weight: 500;
  line-height: normal;
  color: ${({ theme }) => theme.colors.ui.regular};
`;
