import styled from '@emotion/styled';
import { Time } from '@internationalized/date';
import { TimeField } from '@src/ui/components/TimeField';
import * as React from 'react';

type Props = {
  value?: Time;
  onChange: (value: Time) => void;
  leapingDays?: number;
  disabled?: boolean;
  hourCycle?: 12 | 24;
};

export const StopTimeInput: React.FC<Props> = props => {
  return (
    <Root>
      <TimeField
        value={props.value}
        onChange={props.onChange}
        hourCycle={props.hourCycle ?? 24}
        isDisabled={props.disabled}
      />
      {props.leapingDays && <LeapingDays>{props.leapingDays}</LeapingDays>}
    </Root>
  );
};

const Root = styled.div`
  position: relative;
`;

const LeapingDays = styled.span`
  position: absolute;
  display: inline-block;
  min-width: 18px;
  height: 18px;
  line-height: 18px;
  top: -5px;
  right: -5px;
  border-radius: 30px;
  font-size: 12px;
  font-weight: 600;
  text-align: center;
  color: ${({ theme }) => theme.colors.ui_0.regular};
  background-color: ${({ theme }) => theme.colors.ui.regular};
`;
