import * as React from 'react';

export const useListRefs = (
  length: number
): [
  { current: Array<HTMLButtonElement | null> },
  (index: number) => (ref: HTMLButtonElement | null) => void
] => {
  const prevLength = React.useRef(length);
  const refs = React.useRef(new Array(length).fill(null));
  const setRef = React.useCallback(
    index => ref => {
      refs.current[index] = ref;
    },
    []
  );

  if (length !== prevLength.current) {
    prevLength.current = length;
    refs.current = new Array(length).fill(null);
  }

  return [refs, setRef];
};
