import { QueryClient, Updater, useQuery } from '@tanstack/react-query';
import cache from 'webext-storage-cache';

import { TimeEntryRequest, TimeEntryV9 } from '@toggl/track-types';

import { MessageFrom } from '@src/index';
import { sendMessage } from '@src/lib/messaging';

const TE_QUERY_KEY = 'timeEntries';
const CONSTRAINTS_QUERY_KEY = 'timeEntriesConstraints';

const fetch = () =>
  sendMessage({
    type: 'fetch-timeEntries',
    payload: null,
  });

export const start = (entry: TimeEntryRequest, integration: MessageFrom) =>
  sendMessage({
    type: 'start-entry',
    payload: {
      entry,
      from: integration,
    },
  });

export const update = (entry: TimeEntryV9, integration: MessageFrom) =>
  sendMessage({
    type: 'update-entry',
    payload: {
      entry,
      from: integration,
    },
  });

export const remove = (entry: TimeEntryV9) =>
  sendMessage({
    type: 'delete-entry',
    payload: entry,
  });

const fetchTimeEntriesConstraints = (workspaceId: number) =>
  sendMessage({
    type: 'fetch-timeEntriesConstraints',
    payload: {
      workspaceId,
    },
  });

export const changeActionIcon = (active: boolean) =>
  sendMessage({
    type: 'change-action-icon',
    payload: active,
  });

export const useTimeEntries = () => {
  return useQuery([TE_QUERY_KEY], fetch, { staleTime: 60 * 5000 });
};

export const invalidateQuery = (queryClient: QueryClient) => {
  return queryClient.invalidateQueries([TE_QUERY_KEY]);
};

export const cancelQuery = (queryClient: QueryClient) => {
  return queryClient.cancelQueries([TE_QUERY_KEY]);
};

export const getQueryData = (queryClient: QueryClient) => {
  return queryClient.getQueryData<Record<number, TimeEntryV9>>([TE_QUERY_KEY]);
};

export const setQueryData = (
  queryClient: QueryClient,
  updater: Updater<
    Record<number, TimeEntryV9> | undefined,
    Record<number, TimeEntryV9> | undefined
  >
) => {
  return queryClient.setQueryData([TE_QUERY_KEY], updater);
};

export const useTimeEntriesConstraints = (workspaceId: number | undefined) => {
  return useQuery(
    [CONSTRAINTS_QUERY_KEY, workspaceId],
    () => fetchTimeEntriesConstraints(workspaceId ?? 0),
    { staleTime: Infinity, enabled: !!workspaceId }
  );
};
