import { useKeyboard } from '@react-aria/interactions';
import styled from '@emotion/styled';
import { useDuration } from '@src/lib/hooks/useDuration';
import { formatDuration } from '@src/lib/timerUtils';
import { DurationInput } from '@src/ui/components';
import { DateTime } from 'luxon';
import * as React from 'react';

type Props = {
  start: string;
  updateDuration: (e: React.FocusEvent<HTMLInputElement, Element>) => void;
  setDropdownOpen: (open: boolean) => void;
};

export function TimerDuration({
  start,
  updateDuration,
  setDropdownOpen,
}: Props) {
  const duration = useDuration(start);
  const [durationInput, setDurationInput] = React.useState('0:00:00');
  const [inputFocused, setInputFocused] = React.useState(false);
  const inputRef = React.useRef<HTMLInputElement>(null);

  const handleBlur = e => {
    updateDuration(e);
    setInputFocused(false);
  };

  const handleFocus = () => {
    setDurationInput(
      formatDuration(
        start,
        DateTime.fromISO(start).plus({ seconds: duration }).toISO()
      )
    );
    inputRef.current?.select();
    setInputFocused(true);
  };

  const { keyboardProps } = useKeyboard({
    onKeyDown(e) {
      if (e.key === 'Enter') {
        inputRef.current?.blur();
        setDropdownOpen(false);
      } else if (e.key === 'Escape') {
        e.preventDefault();
        setDurationInput(start ? formatDuration(start) : '0:00:00');
        setDropdownOpen(false);
      }
    },
  });

  return (
    <StyledDuration
      {...keyboardProps}
      data-heap-id='extension:edit-time-entry'
      ref={inputRef}
      value={
        inputFocused
          ? durationInput
          : formatDuration(
              start,
              DateTime.fromISO(start).plus({ seconds: duration }).toISO()
            )
      }
      onFocus={handleFocus}
      onChange={e => setDurationInput(e.target.value)}
      onBlur={handleBlur}
    />
  );
}

const StyledDuration = styled(DurationInput)`
  border-radius: 8px;
  height: 32px;
  width: 80px;
  padding-right: 0;
  cursor: pointer;
  border: none;
  padding: 0 0.8rem;
  font-weight: ${({ theme }) => theme.text.semibold};
  font-size: 16px;
  color: ${({ theme }) => theme.colors.ui_60.regular};

  &:hover {
    background-color: ${({ theme }) => theme.colors.surface_secondary.hover};
  }
`;
