import * as React from 'react';
import { useQueryClient } from '@tanstack/react-query';

import { TimeEntryV9 } from '@toggl/track-types';

import * as userService from '@src/services/user';
import * as timeEntryService from '@src/services/timeEntry';
import * as Browser from 'webextension-polyfill';

import { ExtensionContextValue, ViewType } from '@src/context';

export default function useContextState(): ExtensionContextValue {
  const queryClient = useQueryClient();

  const { data: user, isLoading: isLoadingUser } = userService.useUser();

  const {
    data: timeEntries,
    refetch,
    isLoading: isLoadingEntries,
  } = timeEntryService.useTimeEntries();

  React.useEffect(() => {
    // we don't need to do this if this is running in the popup
    if (
      window.location.href.includes('newTab/index.html') ||
      (!window.location.href.includes('chrome-extension://') &&
        !window.location.href.includes('moz-extension://'))
    )
      Browser.storage.local.onChanged.addListener(changes => {
        if (changes.timeEntries) {
          const newEntries = (
            changes.timeEntries as {
              oldValue: unknown;
              newValue: Record<number, TimeEntryV9>;
            }
          ).newValue;
          timeEntryService.setQueryData(queryClient, newEntries);
          setRunningEntry(
            Object.values(newEntries).find(te => te.duration < 0)
          );
        }
      });

    Browser.runtime.onMessage.removeListener(message => {
      if (message.type === 'updateTEs')
        void timeEntryService.invalidateQuery(queryClient);
    });
    Browser.runtime.onMessage.addListener(message => {
      if (message.type === 'updateTEs')
        void timeEntryService.invalidateQuery(queryClient);
    });
  }, [queryClient]);

  const { data: workspaces } = userService.useWorkspaces();
  const { data: workspacePreferences } = userService.useWorkspacePreferences(
    user?.default_workspace_id
  );
  const { data: organizationFeatures } = userService.useOrganizationFeatures(
    !!user
  );
  const [view, changeView] = React.useState<ViewType>('default');
  const [runningEntry, setRunningEntry] = React.useState<
    TimeEntryV9 | undefined
  >(undefined);
  const [editingEntry, setEditingEntry] = React.useState<
    TimeEntryV9 | undefined
  >(undefined);

  const changeEditingEntry = (entry: TimeEntryV9 | undefined) => {
    setEditingEntry(entry);
  };

  const timeEntriesConstraintsEnabled = (workspaceId: number) => {
    const organizationId = workspaces?.[workspaceId]?.organization_id ?? 0;

    return (
      !!organizationId &&
      !!organizationFeatures &&
      userService.isFeatureEnabled(
        'time_entries_constraints',
        organizationId,
        organizationFeatures
      )
    );
  };

  React.useEffect(() => {
    if (timeEntries)
      setRunningEntry(Object.values(timeEntries).find(te => te.duration < 0));
  }, [timeEntries]);

  React.useEffect(() => {
    void timeEntryService.changeActionIcon(!!runningEntry);
  }, [runningEntry]);

  return {
    loading: isLoadingUser || isLoadingEntries,
    view,
    user,
    timeEntries,
    workspaces,
    defaultWorkspace: workspaces?.[user?.default_workspace_id ?? -1],
    workspacePreferences,
    runningEntry,
    editingEntry,
    timeEntriesConstraintsEnabled,
    changeView,
    changeEditingEntry,
    refetchEntries: refetch,
  };
}
