import { PredefinedRange, RangeDatesMap } from '@toggl/track-types';
import moment from 'moment';
import * as React from 'react';
import {
  FormattedMessage,
  MessageDescriptor,
  defineMessages,
} from 'react-intl';

import type { CalendarDate } from './types';

// MIN is dictated by the API
export const minDate: moment.Moment = moment('2006-01-01');
// Actually API MAX is currently 2030 (as of 2019)
// Max date chosen by #project-calendar, figured 2+ years into the future was more than enough
export const maxDate: moment.Moment = moment().add(2, 'years').endOf('year');

export const isOutsideApi = (day: moment.Moment): boolean =>
  day.isBefore(minDate) || day.isAfter(maxDate);
export const getOutsideApiReason = (
  day: moment.Moment
): null | React.ReactNode =>
  isOutsideApi(day) ? (
    <FormattedMessage
      id='CalendarPicker.outsideApiRange'
      defaultMessage='This date is outside the selectable range'
    />
  ) : null;

/** For CalendarRangePicker with two months visible at a time */
export const getInitialVisibleMonth = (
  firstVisibleMonth: moment.Moment
): moment.Moment => {
  // If next month is disabled, don't show it
  if (firstVisibleMonth && maxDate.isSame(firstVisibleMonth, 'month')) {
    return firstVisibleMonth.subtract(1, 'month');
  }

  // initialVisibleMonth prop function requires a Moment, null not allowed
  return firstVisibleMonth;
};

// Check if masked date input has any numbers
export const isEmpty = (val: string): boolean => !val.match(/([0-9])/);
// Check if masked date input has any sections in progress, ex. "01/01/2   "
export const isInProgress = (val: string): boolean =>
  val.replace(/\D/g, '').length < 8;

const tokenMap = {
  Y: 'year',
  M: 'month',
  D: 'date',
};

const getDateParts = (
  value: string,
  dateFormat: string
): { year: number | null; month: number | null; date: number | null } => {
  const values = (value.match(/([0-9]|\s)+/g) || []).map(x => {
    const int = parseInt(x, 10);
    return !isNaN(int) ? int : null;
  });
  const tokens = (dateFormat.match(/(D|M|Y)+/g) || []).map(
    token => tokenMap[token[0]]
  );
  return tokens.reduce((result, name, index) => {
    return {
      ...result,
      [name]: values[index],
    };
  }, {});
};

export const parseDateInput = (
  value: string,
  dateFormat: string,
  now: moment.Moment = moment(),
  min: moment.Moment = minDate,
  max: moment.Moment = maxDate
): moment.Moment => {
  // Using strict mode will disallow Moment from parsing partial dates
  // We're using strict mode because `moment().parsingFlags().parsedDateParts` returns incorrect values
  // if the missing part(s) are at the beginning of the string
  // Hence getting the date parts manually
  let momentValue = moment(value, dateFormat, true);
  if (!momentValue.isValid) {
    const parts = getDateParts(value, dateFormat);
    const year =
      parts.year !== null
        ? Math.max(min.year(), Math.min(parts.year, max.year()))
        : now.year();
    const month =
      parts.month !== null
        ? Math.max(0, Math.min(parts.month - 1, 11)) // month values are an index
        : year === now.year()
        ? now.month()
        : 0;
    const date =
      parts.date !== null
        ? Math.max(
            1,
            Math.min(parts.date, moment({ year, month }).daysInMonth())
          )
        : year === now.year() && month === now.month()
        ? now.day()
        : 1;

    momentValue = moment({ year, month, date });
  }

  return momentValue;
};

export const hasRangeChanged = (
  startDateState: CalendarDate,
  endDateState: CalendarDate,
  startDateProp: moment.Moment,
  endDateProp: moment.Moment
): null | boolean =>
  !!startDateState !== !!startDateProp ||
  !!endDateState !== !!endDateProp ||
  (startDateState && !startDateState.isSame(startDateProp, 'day')) ||
  (endDateState && !endDateState.isSame(endDateProp, 'day'));

export const rangeMessages: {
  [name: PredefinedRange]: MessageDescriptor;
} = defineMessages({
  today: {
    id: 'DateRangePeriods.today',
    defaultMessage: 'Today',
  },
  yesterday: {
    id: 'DateRangePeriods.yesterday',
    defaultMessage: 'Yesterday',
  },
  thisWeek: {
    id: 'DateRangePeriods.thisWeek',
    defaultMessage: 'This week',
  },
  prevWeek: {
    id: 'DateRangePeriods.prevWeek',
    defaultMessage: 'Last week',
  },
  thisMonth: {
    id: 'DateRangePeriods.thisMonth',
    defaultMessage: 'This month',
  },
  prevMonth: {
    id: 'DateRangePeriods.prevMonth',
    defaultMessage: 'Last month',
  },
  last30Days: {
    id: 'DateRangePeriods.last30Days',
    defaultMessage: 'Last 30 days',
  },
  last90Days: {
    id: 'DateRangePeriods.last90Days',
    defaultMessage: 'Last 90 days',
  },
  last12Months: {
    id: 'DateRangePeriods.last12Months',
    defaultMessage: 'Last 12 mths',
  },
  thisYear: {
    id: 'DateRangePeriods.thisYear',
    defaultMessage: 'This year',
  },
  prevYear: {
    id: 'DateRangePeriods.prevYear',
    defaultMessage: 'Last year',
  },
});

export const getRangeDates = (): Record<
  keyof RangeDatesMap,
  { startDate: moment.Moment; endDate: moment.Moment }
> => ({
  today: {
    startDate: moment().startOf('day'),
    endDate: moment().endOf('day'),
  },
  yesterday: {
    startDate: moment().startOf('day').subtract(1, 'day'),
    endDate: moment().endOf('day').subtract(1, 'day'),
  },
  thisWeek: {
    startDate: moment().startOf('week'),
    endDate: moment().endOf('week'),
  },
  prevWeek: {
    startDate: moment().startOf('week').subtract(7, 'day'),
    endDate: moment().endOf('week').subtract(7, 'day'),
  },
  thisMonth: {
    startDate: moment().startOf('month'),
    endDate: moment().endOf('month'),
  },
  prevMonth: {
    startDate: moment().startOf('month').subtract(1, 'month'),
    endDate: moment().subtract(1, 'month').endOf('month'),
  },
  last30Days: {
    startDate: moment().startOf('day').subtract(29, 'day'),
    endDate: moment().endOf('day'),
  },
  last90Days: {
    startDate: moment().startOf('day').subtract(89, 'day'),
    endDate: moment().endOf('day'),
  },
  last12Months: {
    startDate: moment().startOf('month').subtract(11, 'month'),
    endDate: moment().endOf('month'),
  },
  thisYear: {
    startDate: moment().startOf('year'),
    endDate: moment().endOf('year'),
  },
  prevYear: {
    startDate: moment().startOf('year').subtract(1, 'year'),
    endDate: moment().endOf('year').subtract(1, 'year'),
  },
});

export const ranges = Object.keys(getRangeDates());
export const getRangesData = (): {
  id: PredefinedRange;
  label: MessageDescriptor;
  startDate: moment.Moment;
  endDate: moment.Moment;
}[] => {
  const rangeDates = getRangeDates();
  return ranges.map(range => ({
    id: range,
    label: rangeMessages[range],
    ...rangeDates[range],
  }));
};
