import * as React from 'react';
import { DateTime } from 'luxon';
import { useMutation, useQueryClient } from '@tanstack/react-query';

import { TimeEntryRequest, TimeEntryV9 } from '@toggl/track-types';

import { useExtensionContext } from '@src/context';

import * as timeEntryService from '@src/services/timeEntry';

import * as errorReport from '@src/lib/errorReport';

import { RunningTimer } from './RunningTimer';
import { TimerDropdowns } from './TimerDropdowns';
import { HeaderContainer, TimerContainer } from './styles';

import { AutoTimerForm } from './AutoTimerForm';
import { EditingEntry } from './types';
import TimeEntryDescription from './TimeEntryDescription';
import { track } from '@src/lib/utils';

export type TimerMode = 'manual' | 'auto';

export function Timer({
  hideSwitcher,
  disableAutocomplete,
  heapContext,
  ...props
}: {
  hideSwitcher?: boolean;
  disableAutocomplete?: boolean;
  heapContext?: string;
}) {
  const { runningEntry } = useExtensionContext();

  return runningEntry ? (
    <RunningTimer disableAutocomplete={disableAutocomplete} {...props} />
  ) : (
    <TimerForm
      hideSwitcher={hideSwitcher}
      disableAutocomplete={disableAutocomplete}
      heapContext={heapContext}
      {...props}
    />
  );
}

function TimerForm({
  hideSwitcher,
  disableAutocomplete,
  heapContext,
  ...props
}: {
  hideSwitcher?: boolean;
  disableAutocomplete?: boolean;
  heapContext?: string;
}) {
  const queryClient = useQueryClient();
  const { user } = useExtensionContext();
  const [_timeEntry, setTimeEntry] = React.useState<EditingEntry>({});
  const [description, setDescription] = React.useState('');

  const { mutate: startEntry } = useMutation(
    (entry: TimeEntryRequest) =>
      timeEntryService.start(entry, { type: 'popup' }),
    {
      onMutate: async entry => {
        await timeEntryService.cancelQuery(queryClient);
        const previousEntries = timeEntryService.getQueryData(queryClient);
        timeEntryService.setQueryData(queryClient, old => {
          const newEntries: Record<number, TimeEntryV9> = {};
          if (old) {
            Object.assign(newEntries, old);
          }
          newEntries[-1] = { ...entry, id: -1 } as TimeEntryV9;
          return newEntries;
        });
        return { previousEntries };
      },
      onError: (err, _, context) => {
        timeEntryService.setQueryData(queryClient, context?.previousEntries);
        errorReport.error(err as Error);
      },
      onSuccess: (entry, _, context) => {
        const entries = {
          ...context?.previousEntries,
          [entry.id]: entry,
        };
        delete entries[-1];
        timeEntryService.setQueryData(queryClient, entries);
      },
    }
  );

  const startTimer = React.useCallback(
    (e, te?) => {
      e.preventDefault();
      if (!user) return;
      const start = _timeEntry.start ? new Date(_timeEntry.start) : new Date();

      const _entry = te ?? _timeEntry;
      const entry = {
        at: start.toISOString(),
        billable: false,
        description,
        duration: -parseInt((start.getTime() / 1000).toFixed(0), 10),
        start: start.toISOString(),
        tags: [],
        tag_ids: [],
        workspace_id: user.default_workspace_id as number,
        ..._entry,
      } as unknown as TimeEntryRequest;

      startEntry(entry);
    },
    [_timeEntry, description, startEntry, user]
  );

  const createEntry = e => {
    e.preventDefault();
    if (!user) return;
    const duration = Math.floor(
      (DateTime.fromISO(_timeEntry.stop!).toMillis() -
        DateTime.fromISO(_timeEntry.start!).toMillis()) /
        1000
    );

    const entry = {
      at: DateTime.now().toISO(),
      billable: false,
      duration,
      tags: [],
      tag_ids: [],
      workspace_id: user.default_workspace_id as number,
      ..._timeEntry,
      description,
    } as unknown as TimeEntryRequest;

    startEntry(entry, {
      onSuccess: _entry => {
        setTimeEntry({
          ...entry,
          start: entry.stop,
          stop: DateTime.fromISO(entry.stop!)
            .plus({ seconds: entry?.duration })
            .toISO(),
        });
      },
    });
  };

  const onDatesChange = React.useCallback(
    (start, stop) => setTimeEntry(entry => ({ ...entry, start, stop })),
    [_timeEntry]
  );

  const handleDropdownsChange = React.useCallback(
    (changes: {
      billable: boolean;
      selectedTask?: number;
      selectedProject?: number | null;
      selectedTags?: number[] | null;
    }) => {
      setTimeEntry({
        ..._timeEntry,
        billable: changes.billable,
        tag_ids: changes.selectedTags,
        project_id: changes.selectedProject,
        task_id: changes.selectedTask,
      });
    },
    [_timeEntry]
  );

  const handleAutocompleteChange = React.useCallback(
    (entry, e) => {
      setTimeEntry({
        project_id: entry.project_id,
        task_id: entry.task_id,
        tag_ids: entry.tag_ids,
        billable: entry.billable,
      });
      setDescription(entry.description);

      startTimer(e, {
        project_id: entry.project_id,
        task_id: entry.task_id,
        tag_ids: entry.tag_ids,
        billable: entry.billable,
        description: entry.description,
      });
      track('start-time-entry', { context: 'Autocomplete' });
    },
    [startTimer]
  );

  return (
    <HeaderContainer {...props}>
      <TimerContainer>
        <TimeEntryDescription
          description={description}
          onChange={value => setDescription(value)}
          startTimer={startTimer}
          onSelectTimeEntry={handleAutocompleteChange}
          disableAutocomplete={disableAutocomplete}
        />
        <AutoTimerForm
          setTimeEntry={setTimeEntry}
          timeEntry={_timeEntry}
          startTimer={startTimer}
          createEntry={createEntry}
          onDatesChange={onDatesChange}
          heapContext={heapContext}
        />
      </TimerContainer>

      <TimerDropdowns
        onDropdownsChange={handleDropdownsChange}
        timeEntry={_timeEntry}
      />
    </HeaderContainer>
  );
}
