import * as React from 'react';

import {
  CurrentUser,
  TimeEntryV9,
  Workspace,
  WorkspacePreferences,
} from '@toggl/track-types';

export { default as useContextState } from './state';

export type ViewType = 'default' | 'editEntry';

export type ExtensionContextValue = {
  loading: boolean;
  view: ViewType;
  user: CurrentUser | undefined;
  workspaces: Record<number, Workspace> | undefined;
  timeEntries: Record<number, TimeEntryV9> | undefined;
  defaultWorkspace: Workspace | undefined;
  workspacePreferences: WorkspacePreferences | undefined;
  runningEntry: TimeEntryV9 | undefined;
  editingEntry: TimeEntryV9 | undefined;
  timeEntriesConstraintsEnabled: (workspaceId: number) => boolean;
  changeView: (value: ViewType) => void;
  changeEditingEntry: (value: TimeEntryV9 | undefined) => void;
  refetchEntries: () => void;
};

const initialState: ExtensionContextValue = {
  loading: false,
  view: 'default',
  user: undefined,
  timeEntries: undefined,
  workspaces: undefined,
  defaultWorkspace: undefined,
  workspacePreferences: undefined,
  runningEntry: undefined,
  editingEntry: undefined,
  timeEntriesConstraintsEnabled: (workspaceId: number) => !!workspaceId,
  changeView: (value: ViewType) => value,
  changeEditingEntry: (value: TimeEntryV9 | undefined) => value,
  refetchEntries: () => null,
};

export const ExtensionContext: React.Context<ExtensionContextValue> =
  React.createContext(initialState);

export const useExtensionContext = (): ExtensionContextValue =>
  React.useContext(ExtensionContext);
