import { css } from '@emotion/react';
import { Tooltip } from '@src/ui/components/Tooltip';

import { WarningIcon } from '@src/ui/icons/WarningIcon';
import * as React from 'react';
import {
  defineMessages,
  FormattedListParts,
  FormattedMessage,
} from 'react-intl';
import { Placement } from '@popperjs/core';

const messages = defineMessages({
  cta: {
    id: 'unmetConstraintsTooltip.cta',
    defaultMessage:
      'Fill out the required fields to stop the timer and save this time entry',
  },
  add: {
    id: 'unmetConstraintsTooltip.add',
    defaultMessage: 'Add ',
  },
  description: {
    id: 'unmetConstraintsTooltip.description',
    defaultMessage: 'Description',
  },
  tag: {
    id: 'unmetConstraintsTooltip.tag',
    defaultMessage: 'Tag',
  },
  task: {
    id: 'unmetConstraintsTooltip.task',
    defaultMessage: 'Task',
  },
  project: {
    id: 'unmetConstraintsTooltip.project',
    defaultMessage: 'Project',
  },
});

export const UnmetConstraintsTooltip: React.FC<{
  anchorRef: React.MutableRefObject<null>;
  missingProps: Array<string>;
  onClick: React.MouseEventHandler;
  placement?: Placement;
}> = props => {
  return (
    <Tooltip
      elementRef={props.anchorRef.current!}
      onClick={props.onClick}
      placement={props.placement || 'bottom-start'}
      header={<Header missingProps={props.missingProps} />}
    >
      <FormattedMessage {...messages.cta} />
    </Tooltip>
  );
};

const Header: React.FC<{ missingProps: string[] }> = props => {
  return (
    <div
      css={css`
        display: flex;
        align-items: center;
      `}
    >
      <WarningIcon />
      <FormattedListParts type='conjunction' value={props.missingProps}>
        {parts => (
          <div
            css={css`
              margin-left: 8px;
            `}
          >
            <FormattedMessage {...messages.add} />
            {parts.map((part, index) => {
              return index % 2 === 0 ? (
                <FormattedMessage
                  {...messages[part.value]}
                  key={`${part.value}-${index}`}
                />
              ) : (
                <span key={`${part.value}-${index}`}>{part.value}</span>
              );
            })}
          </div>
        )}
      </FormattedListParts>
    </div>
  );
};
