import styled from '@emotion/styled';
import * as React from 'react';

import Input from './Input';
import type {
  CalendarDate,
  CalendarInputNames,
  CalendarRangePickerProps,
} from './types';

export type InputAreaRef = {
  startDate: HTMLInputElement | null;
  endDate: HTMLInputElement | null;
};

export type InputAreaProps = {
  onEnterKey?: () => void;
  onEscapeKey?: () => void;
  onArrowDownKey?: () => void;
  onTabKey?: (e: React.SyntheticEvent<HTMLDivElement>) => void;
  onChange: (name: CalendarInputNames, value: string) => void;
  onFocusChange?: CalendarRangePickerProps['onFocusChange'];
  startDate: CalendarDate;
  endDate: CalendarDate;
  dateFormat: string;
};

const InputArea = React.forwardRef<InputAreaRef, InputAreaProps>(
  (props, forwardedRef) => {
    const {
      onEnterKey,
      onEscapeKey,
      onArrowDownKey,
      onTabKey,
      onChange,
      onFocusChange,
      startDate,
      endDate,
      dateFormat,
    } = props;

    const startDateRef = React.useRef(null);
    const endDateRef = React.useRef(null);
    const [startValue, setStartValue] = React.useState(
      formatDate(startDate, dateFormat)
    );
    const [endValue, setEndValue] = React.useState(
      formatDate(endDate, dateFormat)
    );
    const [focus, setFocus] = React.useState(null);

    const mask = React.useMemo(
      () => convertFormatToMask(dateFormat),
      [dateFormat]
    );

    const handleChange = React.useCallback(
      evt => {
        const { name, value } = evt.target;

        switch (name) {
          case 'startDate':
            setStartValue(value);
            onChange('startDate', value);
            break;
          case 'endDate':
            setEndValue(value);
            onChange('endDate', value);
            break;
          default:
            throw new Error();
        }
      },
      [onChange]
    );

    const handleFocus = React.useCallback(
      evt => {
        const name = evt.target.name;

        switch (name) {
          case 'startDate':
            setFocus(name);
            onFocusChange && onFocusChange(name);
            break;
          case 'endDate':
            setFocus(name);
            onFocusChange && onFocusChange(name);
            break;
          default:
            throw new Error();
        }
      },
      [onFocusChange]
    );

    const handleBlur = React.useCallback(evt => {
      setFocus(null);
    }, []);

    const handleKeyDown = React.useCallback(
      evt => {
        switch (evt.key) {
          case 'Enter':
            onEnterKey && onEnterKey();
            break;
          case 'Escape':
            onEscapeKey && onEscapeKey();
            break;
          case 'ArrowDown':
            onArrowDownKey && onArrowDownKey();
            break;
          case 'Tab':
            onTabKey && onTabKey(evt);
        }
      },
      [onEnterKey, onEscapeKey, onArrowDownKey, onTabKey]
    );

    if (
      focus !== 'startDate' &&
      startValue !== formatDate(startDate, dateFormat)
    ) {
      setStartValue(formatDate(startDate, dateFormat));
    }

    if (focus !== 'endDate' && endValue !== formatDate(endDate, dateFormat)) {
      setEndValue(formatDate(endDate, dateFormat));
    }

    React.useImperativeHandle(forwardedRef, () => ({
      startDate: startDateRef.current,
      endDate: endDateRef.current,
    }));

    return (
      <Root onKeyDown={handleKeyDown}>
        <Input
          ref={startDateRef}
          name='startDate'
          onChange={handleChange}
          onFocus={handleFocus}
          onBlur={handleBlur}
          tabIndex={0}
          value={startValue}
          mask={mask}
          placeholder={dateFormat}
        />
        <InputSeparator />
        <Input
          ref={endDateRef}
          name='endDate'
          onChange={handleChange}
          onFocus={handleFocus}
          onBlur={handleBlur}
          tabIndex={0}
          value={endValue}
          mask={mask}
          placeholder={dateFormat}
        />
      </Root>
    );
  }
);

InputArea.displayName = 'InputArea';

export default InputArea;

const formatDate = (date, format) => (date && date.format(format)) || '';

const convertFormatToMask = (dateFormat: string) =>
  dateFormat.replace(/[A-Z]/gi, '1');

const Root = styled.div`
  display: flex;
  align-items: center;
`;

const InputSeparator = styled.div`
  height: 1px;
  width: 4px;
  border-top: 1px solid ${({ theme }) => theme.colors.ui.regular};
  margin: 0 8px;
  box-sizing: border-box;
`;
