import * as React from 'react';
import moment from 'moment';

import Day from '../Day';
import type { RangeControlsRef } from '../RangeControls';
import { getOutsideApiReason } from '../utils';

export * from './useFullWeekOffset';
export * from './useListRefs';
export * from './useMaximumDays';
export * from './usePresets';
export * from './useSingleDayVisibleMonth';
export * from './useVisibleMonth';
export * from './useFocusOnDatesChange';
export { default as useRangeNavigation } from './useRangeNavigation';

type GetOutsideReason = (day: moment.Moment) => React.ReactNode | null;

// eslint-disable-next-line react/display-name
const DayHoc = getOutsideReason => (day: moment.Moment, states: Set<string>) =>
  <Day day={day} states={states} />;

export const useOutsideRangeReason = (
  getOutsideReason?: GetOutsideReason,
  InternalDayHoC?: (
    getOutsideReason: GetOutsideReason
  ) => (
    day: moment.Moment,
    sates?: Set<string>
  ) => React.ReactElement<typeof Day>
): [
  (day: moment.Moment) => boolean,
  (day: moment.Moment, states: Set<string>) => React.ReactElement<typeof Day>
] => {
  getOutsideReason = getOutsideReason ?? getOutsideApiReason;
  const isOutsideRange = React.useCallback(
    day => !!getOutsideReason!(day),
    [getOutsideReason]
  );
  const renderDayContents = React.useMemo(
    () =>
      InternalDayHoC
        ? InternalDayHoC(getOutsideReason!)
        : DayHoc(getOutsideReason),
    [InternalDayHoC, getOutsideReason]
  );
  return [isOutsideRange, renderDayContents];
};

export const useTodayHighlighted = (): ((day: moment.Moment) => boolean) => {
  const today = React.useMemo(() => moment().startOf('d'), []);

  return React.useCallback(day => day.startOf('d').isSame(today), [today]);
};

export const useFocusOnOpenEffect = (
  open: boolean,
  elementRef: { current: null | RangeControlsRef },
  focusedInput: 'startDate' | 'endDate'
) => {
  React.useEffect(() => {
    if (open && elementRef.current?.[focusedInput]) {
      elementRef.current[focusedInput]?.select();
    }
  }, [elementRef, focusedInput, open]);
};

/*
 * In case we want to disable the default keyboard navigation inside the calendar days
 * we use this effect so we can skip the month days when tabbing around.
 *
 * NOTE: The days are still capable to be focused using JavaScript, that why we set the tabIndex to -1
 *
 */
export function useDisableKeyboardNavigationEffect(
  ref: { current: null | HTMLDivElement },
  allowNavigation?: boolean
) {
  React.useLayoutEffect(() => {
    if (ref.current && !allowNavigation) {
      const selectedDay = ref.current.querySelector(
        '.CalendarDay[tabindex="0"]'
      );

      if (selectedDay) {
        selectedDay.setAttribute('tabindex', '-1');
      }
    }
  }, [ref, allowNavigation]);
}

export type DayPickerInstance = {
  startDayNavigation: () => void;
};

export const useImperativeNavigationHandler = (
  handlerRef: React.Ref<DayPickerInstance>,
  domRef: { current: null | HTMLDivElement }
) => {
  React.useImperativeHandle(
    handlerRef,
    () => ({
      startDayNavigation: () => {
        const lastFocus = domRef.current?.querySelector(
          '.CalendarDay[tabindex="0"]'
        ) as HTMLElement;

        if (lastFocus) {
          lastFocus.focus();
          return;
        }

        const today = domRef.current?.querySelector(
          '.CalendarDay__selected'
        ) as HTMLElement;

        if (today) {
          today.focus();
        }
      },
    }),
    [domRef]
  );
};
