import styled from '@emotion/styled';
import * as React from 'react';

import { useListRefs } from './hooks';
import { RangePresetItem } from './RangePresetItem';
import type { CalendarRangePreset } from './types';

type RangePresetsProps = {
  showDefaultPreset?: boolean;
  onSelect: (CalendarRangePreset) => void;
  onSetDefault: (CalendarRangePreset) => void;
  onArrowUpKey?: () => void;
  presets: CalendarRangePreset[];
  className?: string;
};

type RangePresetsRef = {
  focus: () => void;
};

const CalendarRangePresets = React.forwardRef<
  RangePresetsRef,
  RangePresetsProps
>((props, ref) => {
  const { presets, showDefaultPreset, onSelect, onSetDefault, onArrowUpKey } =
    props;
  const [refs, setRef] = useListRefs(presets.length);
  const handleArrowKey = React.useCallback(
    newIndex => {
      // Cancel if newIndex is outside presets array
      if (newIndex < 0) {
        if (onArrowUpKey) onArrowUpKey();
        return;
      }
      if (newIndex > presets.length) return;
      // Focus new preset
      if (refs.current[newIndex]) refs.current[newIndex]?.focus();
    },
    [onArrowUpKey, presets.length, refs]
  );

  React.useImperativeHandle(ref, () => ({
    focus: () => {
      if (refs.current[0]) refs.current[0].focus();
    },
  }));

  return (
    <RangePresetsRoot className={props.className}>
      {presets.map((preset, index) => (
        <RangePresetItem
          key={index}
          ref={setRef(index)}
          index={index}
          preset={preset}
          showDefaultPreset={showDefaultPreset}
          onSelect={onSelect}
          onSetDefault={onSetDefault}
          onArrowKey={handleArrowKey}
        />
      ))}
    </RangePresetsRoot>
  );
});

CalendarRangePresets.displayName = 'CalendarRangePresets';

export default CalendarRangePresets;

const RangePresetsRoot = styled.div`
  margin: 0;
  padding: 12px 16px;
`;
