import { Global, css } from '@emotion/react';
import { theme as AppTheme } from '@src/ui/theme';
import * as React from 'react';
import datePickerStyles from 'react-dates/lib/css/_datepicker.css';

const globalStyles = (theme: typeof AppTheme['light']) => css`
  ${datePickerStyles};

  .DayPicker__horizontal {
    background-color: ${theme.colors.surface_primary.regular};
  }

  .DayPicker_weekHeader {
    top: 68px;
  }

  .DayPicker_weekHeader_li small {
    color: ${theme.colors.ui_50.regular};
    font-size: 12px;
  }

  .DayPickerNavigation_button__disabled,
  .DayPickerNavigation_button__disabled:hover {
    /* Disable default border, because we're inserting our own button components and this border shows in a strange place. */
    border: none;
  }

  /* 'text-align' is always 'right' and we change the width to move the content:
  0 = left aligned,
  100% = right aligned */
  .CalendarMonth {
    background-color: ${theme.colors.surface_primary.regular};
  }

  .CalendarMonthGrid {
    background-color: ${theme.colors.surface_primary.regular};
  }

  .CalendarMonth_caption {
    padding-bottom: 42px;
  }

  .CalendarMonth_caption strong {
    font-size: 24px;
    font-weight: 200;
    color: ${theme.colors.ui.regular};
  }

  .CalendarMonth_table tr {
    height: 42px;
    border: none;
    border-top: 1px solid ${theme.colors.background.regular};
  }

  .CalendarDay__default {
    padding: 4px 0;
  }

  .CalendarDay__default .Day {
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    color: ${theme.colors.ui.regular};
  }

  .CalendarDay__outside .Day {
    color: ${theme.colors.surface_secondary.regular};
  }

  .CalendarDay__default:not(.CalendarDay__hovered_offset):not(.CalendarDay__hovered_span):not(.CalendarDay__selected_span):not(.CalendarDay__selected)
    .Day:hover {
    background-color: ${theme.colors.surface_secondary.hover};
    border-radius: 8px;
  }

  .CalendarDay__default:focus .Day {
    background-color: ${theme.colors.background.regular};
    color: ${theme.colors.surface_primary.regular};
    border-radius: 8px;
  }

  .CalendarDay__default .Day .Day__inner {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 28px;
    height: 28px;
    margin: 0;
    padding: 0;
    border-radius: 50%;
    line-height: 1;
  }

  /* Reset everything the styles should now be applied to the Day child */
  .CalendarDay__default,
  .CalendarDay__default:hover,
  .CalendarDay__selected,
  .CalendarDay__selected:active,
  .CalendarDay__selected:hover,
  .CalendarDay__hovered_span:hover,
  .CalendarDay__selected_span,
  .CalendarDay__selected_span:hover,
  .CalendarDay__hovered_span {
    background: none;
    border: none;
  }

  .CalendarDay__selected_span .Day,
  .CalendarDay__hovered_offset .Day,
  .CalendarDay__hovered_span .Day {
    /* TODO-REBRAND: Remove conditional theming */
    background: ${theme.colors.ui.regular};
    color: ${theme.colors.ui.regular};
    border-radius: 0;
  }

  .CalendarDay__highlighted_calendar:not(.CalendarDay__hovered_span)
    .Day
    .Day__inner {
    background-color: ${theme.colors.surface_secondary.regular};
    color: ${theme.colors.ui.regular};
  }

  .CalendarDay__highlighted_calendar.CalendarDay__selected_span
    .Day
    .Day__inner,
  .CalendarDay__highlighted_calendar.CalendarDay__hovered_offset
    .Day
    .Day__inner,
  .CalendarDay__highlighted_calendar.CalendarDay__hovered_span
    .Day
    .Day__inner {
    background: ${theme.colors.surface_primary.hover};
    color: ${theme.colors.ui.regular};
  }

  .CalendarDay__highlighted_calendar.CalendarDay__selected_start:not(.CalendarDay__hovered_offset)
    .Day
    .Day__inner,
  .CalendarDay__highlighted_calendar.CalendarDay__selected:not(.CalendarDay__hovered_offset)
    .Day
    .Day__inner,
  .CalendarDay__highlighted_calendar.CalendarDay__selected_end:not(.CalendarDay__hovered_offset)
    .Day
    .Day__inner {
    background: ${theme.colors.accent.regular};
    color: ${theme.colors.surface_primary.regular};
    font-weight: 500;
  }

  .CalendarDay__firstDayOfWeek .Day,
  .CalendarDay__selected_start:not(.CalendarDay__hovered_offset) .Day {
    border-radius: 0 8px 8px 0;
  }

  .CalendarDay__lastDayOfWeek .Day,
  .CalendarDay__selected_end:not(.CalendarDay__hovered_offset) .Day {
    border-radius: 0 8px 8px 0;
  }

  .CalendarDay__selected .Day,
  .CalendarDay__selected:active .Day,
  .CalendarDay__selected:hover .Day,
  .CalendarDay__highlighted_calendar.CalendarDay__selected_start .Day,
  .CalendarDay__highlighted_calendar.CalendarDay__selected_start:hover .Day,
  .CalendarDay__highlighted_calendar.CalendarDay__selected_end .Day,
  .CalendarDay__highlighted_calendar.CalendarDay__selected_end:hover .Day,
  .CalendarDay__selected_end .Day,
  .CalendarDay__selected_end:hover .Day,
  .CalendarDay__selected_start:hover .Day,
  .CalendarDay__selected_start .Day {
    background: ${theme.colors.accent.hover};
    color: ${theme.colors.ui_0.regular};
  }

  .CalendarDay__selected:not(.CalendarDay__hovered_offset):not(.CalendarDay__selected_start):not(.CalendarDay__selected_end)
    .Day,
  .CalendarDay__selected_start.CalendarDay__selected_end .Day {
    border-radius: 8px;
  }

  .CalendarDay__blocked_out_of_range,
  .CalendarDay__blocked_out_of_range:active,
  .CalendarDay__blocked_out_of_range:hover {
    border: none;
    background: transparent;
  }

  .CalendarDay__blocked_out_of_range .Day {
    background: transparent;
  }

  .CalendarDay__blocked_out_of_range:focus .Day {
    background: transparent;
  }

  .CalendarDay__blocked_out_of_range .Day .Day__inner,
  .CalendarDay__blocked_out_of_range:hover .Day .Day__inner {
    cursor: default;
    color: ${theme.colors.ui.regular};
    opacity: 0.3;
    background: transparent;
  }
`;

export default function CalendarGlobalStyles() {
  return <Global styles={globalStyles} />;
}
