import { useTheme, css } from '@emotion/react';
import { useExtensionContext } from '@src/context';
import { checkUnmetConstraints } from '@src/lib/utils';
import { IconStop } from '@src/ui/icons/TimerFormIcons';
import { useQueryClient, useMutation } from '@tanstack/react-query';
import { TimeEntryV9 } from '@toggl/track-types';
import { DateTime } from 'luxon';
import * as React from 'react';
import { DateTimeInput } from '../dateTimeInput/DateTimeInput';
import Dropdown from '../Dropdown';
import { UnmetConstraintsTooltip } from '../UnmetConstraintsTooltip';
import {
  HeaderContainer,
  RunningTimerDescription,
  StyledTimerButton,
  TimerContainer,
} from './styles';
import isEqual from 'lodash.isequal';

import * as tagService from '@src/services/tag';
import * as errorReport from '@src/lib/errorReport';
import * as timeEntryService from '@src/services/timeEntry';

import { TimerDuration } from './TimerDuration';
import { TimerDropdowns } from './TimerDropdowns';
import TimeEntryDescription from './TimeEntryDescription';
import { calculateStartDate, parseDuration } from '@src/lib/timerUtils';

let debouncerInterval;

export function RunningTimer({
  disableAutocomplete,
  ...props
}: {
  disableAutocomplete?: boolean;
}) {
  const theme = useTheme();
  const firstRender = React.useRef(true);
  const { timeEntriesConstraintsEnabled, runningEntry } = useExtensionContext();
  const [description, setDescription] = React.useState(
    runningEntry?.description ?? ''
  );
  const [dropdownOpen, setDropdownOpen] = React.useState(false);

  const [showUnmetTooltip, setShowUnmetTooltip] = React.useState(false);

  const stopButtonRef = React.useRef(null);
  const descriptionRef = React.useRef<HTMLInputElement>(null);

  const queryClient = useQueryClient();

  const { data: tags, refetch: refetchTags } = tagService.useTags();
  const { data: timeEntriesConstraints } =
    timeEntryService.useTimeEntriesConstraints(runningEntry?.workspace_id);

  const [_runningEntry, setRunningEntry] = React.useState(runningEntry!);
  React.useEffect(() => {
    if (runningEntry?.id !== _runningEntry.id) {
      setRunningEntry(runningEntry!);
      runningEntry && setDescription(runningEntry.description);
    }
  }, [_runningEntry.id, runningEntry]);

  const { mutateAsync: updateEntry } = useMutation(
    (_entry: TimeEntryV9) => timeEntryService.update(_entry, { type: 'popup' }),
    {
      onMutate: async entry => {
        await timeEntryService.cancelQuery(queryClient);
        const previousEntries = timeEntryService.getQueryData(queryClient);
        timeEntryService.setQueryData(queryClient, old => ({
          ...old,
          [entry.id]: entry,
        }));
        return { previousEntries };
      },
      onError: (err, _newEntry, context) => {
        timeEntryService.setQueryData(queryClient, context?.previousEntries);
        errorReport.error(err as Error);
      },
      onSuccess: (entry, _, context) =>
        timeEntryService.setQueryData(queryClient, {
          ...context?.previousEntries,
          [entry.id]: entry,
        }),
    }
  );

  React.useEffect(() => {
    // TODO: try to remove this ref, it was needed because this effect was being running in the first render
    // Causing an undesired PUT request every time we open the popup

    if (
      _runningEntry.id <= 0 ||
      firstRender.current ||
      isEqual(_runningEntry, runningEntry)
    ) {
      firstRender.current = false;
      return;
    }

    if (debouncerInterval) clearTimeout(debouncerInterval);
    debouncerInterval = setTimeout(() => updateEntry(_runningEntry), 400);

    return () => clearInterval(debouncerInterval);
  }, [_runningEntry, updateEntry]);

  const missingProps = React.useMemo(
    () =>
      runningEntry &&
      checkUnmetConstraints(timeEntriesConstraints, runningEntry),
    [runningEntry, timeEntriesConstraints]
  );

  React.useEffect(() => {
    let hasInvalidTag = false;
    if (runningEntry?.tag_ids && tags) {
      runningEntry.tag_ids.forEach(tag => {
        if (tags[tag]?.workspace_id !== runningEntry.workspace_id)
          hasInvalidTag = true;
      });
    }

    if (hasInvalidTag) refetchTags();
  }, [runningEntry?.tag_ids, runningEntry?.workspace_id, refetchTags, tags]);

  const handleStopClick: React.MouseEventHandler = React.useCallback(
    e => {
      e.preventDefault();
      e.stopPropagation();
      if (!_runningEntry) return;

      if (
        missingProps?.length &&
        timeEntriesConstraintsEnabled(_runningEntry.workspace_id)
      )
        setShowUnmetTooltip(true);
      else {
        const stopTime = new Date();
        const startTime = new Date(_runningEntry.start);
        const timeEntry = {
          stop: stopTime.toISOString(),
          duration: Math.floor(
            (stopTime.getTime() - startTime.getTime()) / 1000
          ),
        };
        return setRunningEntry({
          ..._runningEntry,
          tag_ids: _runningEntry.tag_ids ?? [],
          ...timeEntry,
        });
      }
    },
    [missingProps?.length, timeEntriesConstraintsEnabled, _runningEntry]
  );

  const handleDatesChange = start =>
    runningEntry &&
    runningEntry.id >= 0 &&
    setRunningEntry({
      ..._runningEntry,
      tag_ids: _runningEntry.tag_ids ?? [],
      start,
    });

  const updateDuration = (e: React.FocusEvent<HTMLInputElement, Element>) => {
    const start =
      calculateStartDate(parseDuration(e.target.value).toMillis() / 1000) ?? '';
    runningEntry &&
      runningEntry.id >= 0 &&
      setRunningEntry({
        ..._runningEntry,
        duration: -parseInt(
          (DateTime.fromISO(start).toMillis() / 1000).toFixed(0),
          10
        ),
        start,
      });
  };

  const handleDescriptionChange = (value: string) => {
    setDescription(value);
  };

  const handleDescriptionBlur = React.useCallback(
    () =>
      _runningEntry &&
      _runningEntry.id >= 0 &&
      setRunningEntry({
        ..._runningEntry,
        tag_ids: _runningEntry.tag_ids ?? [],
        description,
      }),
    [description, _runningEntry]
  );

  const handleDropdownsChange = React.useCallback(
    ({
      billable,
      selectedTask,
      selectedProject,
      selectedTags,
    }: {
      billable: boolean;
      selectedTask?: number | undefined;
      selectedProject?: number | null | undefined;
      selectedTags?: number[] | null | undefined;
    }) =>
      _runningEntry?.id &&
      _runningEntry.id >= 0 &&
      setRunningEntry({
        ..._runningEntry,
        billable,
        task_id: selectedTask,
        project_id: selectedProject,
        tag_ids: selectedTags,
      }),
    [_runningEntry]
  );

  const handleAutocompleteChange = async (te, e) => {
    e.preventDefault();
    if (_runningEntry?.id && _runningEntry.id >= 0) {
      await setRunningEntry({
        ..._runningEntry,
        workspace_id: te.workspace_id,
        billable: te.billable,
        task_id: te.task_id,
        project_id: te.project_id,
        tag_ids: te.tag_ids,
        description: te.description,
      });
      setDescription(te.description);
    }
  };

  return (
    <HeaderContainer {...props}>
      <TimerContainer>
        <RunningTimerDescription>
          <TimeEntryDescription
            ref={descriptionRef}
            running
            description={description}
            onChange={handleDescriptionChange}
            onBlur={handleDescriptionBlur}
            onSelectTimeEntry={handleAutocompleteChange}
            disableAutocomplete={disableAutocomplete}
          />
        </RunningTimerDescription>
        <div
          ref={stopButtonRef}
          css={css`
            position: relative;
          `}
        >
          {_runningEntry?.start && (
            <Dropdown>
              <Dropdown.RawTrigger onFocus={() => setDropdownOpen(true)}>
                <TimerDuration
                  start={_runningEntry.start}
                  updateDuration={updateDuration}
                  setDropdownOpen={setDropdownOpen}
                />
              </Dropdown.RawTrigger>
              <Dropdown.Overlay />
              <Dropdown.DateTimeContent
                open={dropdownOpen}
                position={{ left: -192, top: 40 }}
              >
                <DateTimeInput
                  start={_runningEntry.start ?? DateTime.now().toISO()}
                  stop={DateTime.now().toFormat('HH:mm')}
                  onDatesChange={handleDatesChange}
                  disableStop
                />
              </Dropdown.DateTimeContent>
            </Dropdown>
          )}
          <StyledTimerButton
            theme={theme}
            isStop
            onClick={handleStopClick}
            onMouseEnter={() => setShowUnmetTooltip(true)}
            onMouseLeave={() => setShowUnmetTooltip(false)}
          >
            <IconStop />
          </StyledTimerButton>
          {showUnmetTooltip &&
            timeEntriesConstraints &&
            !!missingProps?.length && (
              <UnmetConstraintsTooltip
                onClick={() => setShowUnmetTooltip(false)}
                missingProps={missingProps}
                anchorRef={stopButtonRef}
              />
            )}
        </div>
      </TimerContainer>
      <TimerDropdowns
        onDropdownsChange={handleDropdownsChange}
        timeEntry={_runningEntry}
      />
    </HeaderContainer>
  );
}
