import moment from 'moment';
import * as React from 'react';

import type { CalendarRangePickerProps } from '../types';

type Props = {
  startDate: CalendarRangePickerProps['startDate'];
  endDate: CalendarRangePickerProps['endDate'];
  onChange: CalendarRangePickerProps['onDatesChange'];
  minDate?: CalendarRangePickerProps['minDate'];
  maxDate?: CalendarRangePickerProps['maxDate'];
};

export default function useRangeNavigation({
  startDate,
  endDate,
  onChange,
  minDate,
  maxDate,
}: Props): {
  nextDisabled: boolean;
  previousDisabled: boolean;
  onNext: () => void;
  onPrevious: () => void;
} {
  const nextRange =
    startDate && endDate ? moveRange(1, startDate, endDate) : null;
  const previousRange =
    startDate && endDate ? moveRange(-1, startDate, endDate) : null;

  const nextDisabled =
    !!nextRange && !!maxDate && moment(maxDate).isBefore(nextRange.endDate);
  const previousDisabled =
    !!previousRange &&
    !!minDate &&
    moment(minDate).isAfter(moment(previousRange.startDate));

  const onNext = React.useCallback(() => {
    if (!nextRange || nextDisabled) return;
    onChange(nextRange);
  }, [nextRange, nextDisabled, onChange]);
  const onPrevious = React.useCallback(() => {
    if (!previousRange || previousDisabled) return;
    onChange(previousRange);
  }, [previousRange, previousDisabled, onChange]);

  return {
    nextDisabled,
    previousDisabled,
    onNext,
    onPrevious,
  };
}

function moveRange(
  direction: 1 | -1,
  startDate: moment.Moment,
  endDate: moment.Moment
) {
  if (isMonthRange(startDate, endDate)) {
    const newStartDate = moment(startDate)
      .add(direction, 'month')
      .startOf('month');
    const newEndDate = moment(endDate).add(direction, 'month').endOf('month');

    return {
      startDate: newStartDate,
      endDate: newEndDate,
    };
  }

  if (isYearRange(startDate, endDate)) {
    const newStartDate = moment(startDate)
      .add(direction, 'year')
      .startOf('year');
    const newEndDate = moment(endDate).add(direction, 'year').endOf('year');

    return {
      startDate: newStartDate,
      endDate: newEndDate,
    };
  }

  const diff = getInclusiveDayDiff(startDate, endDate) * direction;
  const newStartDate = moment(startDate).add(diff, 'd');
  const newEndDate = moment(endDate).add(diff, 'd');
  return {
    startDate: newStartDate,
    endDate: newEndDate,
  };
}

const isMonthRange = (startDate, endDate) =>
  moment(startDate).isSame(endDate, 'month') &&
  moment(startDate).startOf('month').isSame(startDate, 'day') &&
  moment(endDate).endOf('month').isSame(endDate, 'day');

const isYearRange = (startDate, endDate) =>
  moment(startDate).isSame(endDate, 'year') &&
  moment(startDate).startOf('year').isSame(startDate, 'day') &&
  moment(endDate).endOf('year').isSame(endDate, 'day');

const getInclusiveDayDiff = (
  startDate: moment.Moment,
  endDate: moment.Moment
) => {
  const diffStart = moment(startDate).startOf('day');
  const diffEnd = moment(endDate).add(1, 'day').startOf('day');
  return moment.duration(diffEnd.diff(diffStart)).asDays();
};
