import { useExtensionContext } from '@src/context';
import BillableIcon from '@src/ui/icons/BillableIcon';
import { ProjectIcon } from '@src/ui/icons/ProjectIcon';
import TagsIcon from '@src/ui/icons/TagsIcon';
import * as React from 'react';
import { ProjectsDropdown, ProjectCircle } from '../ProjectsDropdown';
import { TagsDropdown } from '../TagsDropdown';
import {
  DropdownsContainer,
  CustomDropdownTrigger,
  SelectedProject,
} from './styles';
import * as tagService from '@src/services/tag';
import * as projectService from '@src/services/project';

import { EditingEntry } from './types';
import { css } from '@emotion/react';

export const TimerDropdowns: React.FC<{
  onDropdownsChange: (values: {
    billable: boolean;
    selectedTask?: number | null;
    selectedProject?: number | null;
    selectedTags?: number[] | null;
  }) => void;
  timeEntry?: Omit<EditingEntry, 'stop'>;
}> = ({ onDropdownsChange, timeEntry }) => {
  const { data: tags } = tagService.useTags();
  const { data: projects, refetch: refetchProjects } =
    projectService.useProjects();
  const { data: tasks } = projectService.useTasks();
  const { user } = useExtensionContext();

  const [selectedProject, setSelectedProject] = React.useState<
    number | null | undefined
  >(timeEntry?.project_id ?? null);
  const [selectedTask, setSelectedTask] = React.useState<
    number | null | undefined
  >(timeEntry?.task_id ?? undefined);
  const [billable, setBillable] = React.useState(timeEntry?.billable ?? false);
  const [selectedTags, setSelectedTags] = React.useState<number[]>(
    (timeEntry?.tag_ids as unknown as number[]) ?? []
  );

  React.useEffect(() => {
    if (timeEntry) {
      setSelectedProject(timeEntry.project_id);
      setSelectedTask(timeEntry.task_id);
      setBillable(timeEntry.billable ?? false);

      if (timeEntry.project_id && projects && !projects[timeEntry.project_id])
        void refetchProjects();
    }

    timeEntry?.tag_ids &&
      timeEntry.tag_ids !== selectedTags &&
      setSelectedTags(timeEntry.tag_ids as number[]);
  }, [selectedTags, timeEntry]);

  const [tagsOpen, setTagsOpen] = React.useState(false);
  const [projectsOpen, setProjectsOpen] = React.useState(false);

  const filteredTags = React.useMemo(() => {
    const wid =
      projects && selectedProject
        ? projects[selectedProject]?.workspace_id
        : user?.default_workspace_id;

    return (
      (tags &&
        Object.values(tags)
          .filter(
            tag =>
              tag.workspace_id === wid || selectedTags.includes(Number(tag.id))
          )
          .sort((t1, t2) => t1?.name?.localeCompare(t2?.name))) ??
      []
    );
  }, [user, projects, selectedProject, selectedTags, tags]);

  const handleTagsChange = (id: number) => {
    const newTags = selectedTags?.includes(id)
      ? selectedTags.filter(tagId => tagId !== id)
      : [id, ...selectedTags!];
    onDropdownsChange({
      billable,
      selectedProject,
      selectedTask,
      selectedTags: newTags,
    });
  };

  const handleProjectsChange = (changes: {
    projectId: number;
    taskId: number;
    billable: boolean;
  }) => {
    setSelectedProject(changes.projectId);
    setSelectedTask(changes.taskId);
    setBillable(changes.billable);
    onDropdownsChange({
      billable: changes.billable,
      selectedTags,
      selectedTask: changes.taskId,
      selectedProject: changes.projectId,
    });
  };

  const handleBillableChange = () => {
    setBillable(!billable);
    onDropdownsChange({
      billable: !billable,
      selectedProject,
      selectedTask,
      selectedTags,
    });
  };

  return (
    <DropdownsContainer>
      <ProjectsDropdown
        contentPosition={{ left: 0, top: 40 }}
        customTrigger={
          <CustomDropdownTrigger highlight={!!selectedProject || projectsOpen}>
            {projects && selectedProject ? (
              <SelectedProject color={projects[selectedProject]?.color}>
                <ProjectCircle color={projects[selectedProject]?.color} />
                <span>{projects[selectedProject]?.name}</span>
                {selectedTask && tasks && (
                  <span
                    css={css`
                      overflow: hidden;
                      text-overflow: ellipsis;
                    `}
                  >{`: ${tasks[selectedTask].name}`}</span>
                )}
              </SelectedProject>
            ) : (
              <ProjectIcon />
            )}
          </CustomDropdownTrigger>
        }
        selectedProjectId={selectedProject}
        selectedTaskId={selectedTask}
        onChange={handleProjectsChange}
        onOpenChange={open => setProjectsOpen(open)}
      />
      <TagsDropdown
        contentPosition={{ left: -45, top: 40 }}
        customTrigger={
          <CustomDropdownTrigger highlight={!!selectedTags?.length || tagsOpen}>
            <TagsIcon title='tags' />
          </CustomDropdownTrigger>
        }
        options={filteredTags}
        selectedIds={selectedTags ?? []}
        onChange={handleTagsChange}
        onOpenChange={open => setTagsOpen(open)}
      />
      <CustomDropdownTrigger
        highlight={billable}
        onClick={handleBillableChange}
        data-heap-id='extension:mark-billable'
      >
        <BillableIcon />
      </CustomDropdownTrigger>
    </DropdownsContainer>
  );
};
