import { QueryClient, Updater, useQuery } from '@tanstack/react-query';

import { sendMessage } from '@src/lib/messaging';
import { Tag } from '@toggl/track-types';

const fetch = () =>
  sendMessage({
    type: 'fetch-tags',
    payload: null,
  });

export const create = (payload: { name: string; workspaceId: number }) => {
  return sendMessage({ type: 'create-tag', payload });
};

export const useTags = (enabled = true) => {
  return useQuery(['tags'], fetch, {
    staleTime: 5000 * 60,
    enabled,
  });
};

export const cancelQuery = (queryClient: QueryClient) => {
  return queryClient.cancelQueries(['tags']);
};

export const getQueryData = (queryClient: QueryClient) => {
  return queryClient.getQueryData<Record<number, Tag>>(['tags']);
};

export const setQueryData = (
  queryClient: QueryClient,
  updater: Updater<
    Record<number, Tag> | undefined,
    Record<number, Tag> | undefined
  >
) => {
  return queryClient.setQueryData(['tags'], updater);
};
