import styled from '@emotion/styled';
import * as React from 'react';

import Input from './Input';
import type { CalendarDate } from './types';

export type SingleDayInputAreaRef = {
  date: HTMLInputElement | null;
};

export type SingleDayInputAreaProps = {
  onEnterKey?: () => void;
  onEscapeKey?: () => void;
  onArrowDownKey?: () => void;
  onTabKey?: (e: React.KeyboardEvent<HTMLDivElement>) => void;
  onChange: (value: string) => void;
  date: CalendarDate;
  dateFormat: string;
};

const SingleDayInputArea = React.forwardRef<
  { date: null | HTMLInputElement },
  SingleDayInputAreaProps
>((props, forwardedRef) => {
  const {
    onEnterKey,
    onEscapeKey,
    onArrowDownKey,
    onTabKey,
    onChange,
    date,
    dateFormat,
  } = props;

  const dateRef = React.useRef(null);
  const [value, setValue] = React.useState(formatDate(date, dateFormat));
  const [focus, setFocus] = React.useState(false);

  const mask = React.useMemo(
    () => convertFormatToMask(dateFormat),
    [dateFormat]
  );

  const handleChange = React.useCallback(
    evt => {
      const value = evt.target.value;

      // Update field locally
      setValue(value);
      // Send field update
      onChange(value);
    },
    [onChange]
  );

  const handleFocus = React.useCallback(() => {
    setFocus(true);
  }, []);

  const handleBlur = React.useCallback(() => {
    setFocus(false);
  }, []);

  const handleKeyDown = React.useCallback(
    (evt: React.KeyboardEvent<HTMLDivElement>) => {
      switch (evt.key) {
        case 'Enter':
          onEnterKey && onEnterKey();
          break;
        case 'Escape':
          onEscapeKey && onEscapeKey();
          break;
        case 'ArrowDown':
          onArrowDownKey && onArrowDownKey();
          break;
        case 'Tab':
          onTabKey && onTabKey(evt);
      }
    },
    [onEnterKey, onEscapeKey, onArrowDownKey, onTabKey]
  );

  if (!focus && value !== formatDate(date, dateFormat)) {
    setValue(formatDate(date, dateFormat));
  }

  React.useImperativeHandle(forwardedRef, () => ({
    date: dateRef.current,
  }));

  return (
    <Root onKeyDown={handleKeyDown}>
      <Input
        ref={dateRef}
        name='date'
        onChange={handleChange}
        onFocus={handleFocus}
        onBlur={handleBlur}
        tabIndex={0}
        value={value}
        mask={mask}
        placeholder={dateFormat}
      />
    </Root>
  );
});

SingleDayInputArea.displayName = 'SingleDayInputArea';

export default SingleDayInputArea;

const formatDate = (date: CalendarDate, format: string) =>
  (date && date.format(format)) || '';

const convertFormatToMask = (dateFormat: string) =>
  dateFormat.replace(
    /\[(.*?)\]|[A-Z]/gi,
    (match, escapedGroup) =>
      escapedGroup
        ? escapedGroup.replace(/(A)/gi, '\\$1') // escape A/a (letter mask) inside []
        : '1' // replace any letter outside [] with '1' (number mask)
  );

const Root = styled.div`
  display: flex;
  align-items: center;
`;
