import { ClassNames } from '@emotion/react';
import styled from '@emotion/styled';
import * as React from 'react';
import BadMaskedInput from 'react-maskedinput';

type Props = {
  mask: string;
  name?: string;
  onBlur?: (e: React.MouseEvent<HTMLInputElement>) => void;
  onChange?: (e: React.MouseEvent<HTMLInputElement>) => void;
  onFocus?: (e: React.MouseEvent<HTMLInputElement>) => void;
  placeholder: string;
  tabIndex?: number;
  value: string;
};

const InputDefault = React.forwardRef<HTMLInputElement | null, Props>(
  (props, forwardedRef) => (
    <ClassNames>
      {({ css }) => {
        const hiddenNode: string = css``;

        return (
          <Input
            {...props}
            forwardedRef={forwardedRef}
            hiddenNode={hiddenNode}
          />
        );
      }}
    </ClassNames>
  )
);

InputDefault.displayName = 'InputDefault';

export default InputDefault;

const Input: React.FC<{
  forwardedRef: React.Ref<HTMLInputElement>;
  hiddenNode: React.ReactNode;
  value: string;
  placeholder: string;
}> = ({ forwardedRef, hiddenNode, ...props }) => {
  const width = useAutosizeInput(
    forwardedRef,
    hiddenNode,
    props.value,
    props.placeholder
  );

  return <EnhancedMaskedInput {...props} ref={forwardedRef} width={width} />;
};

const MaskedInput = React.forwardRef<
  HTMLInputElement | null,
  { width: number | null }
>((props, forwardedRef) => {
  const ref = React.useRef<null | { input: HTMLInputElement }>(null);

  React.useImperativeHandle(
    forwardedRef,
    () => ref.current?.input ?? (null as unknown as HTMLInputElement)
  );
  return <BadMaskedInput {...props} ref={ref} placeholderChar=' ' />;
});

MaskedInput.displayName = 'MaskedInput';

const EnhancedMaskedInput = styled(MaskedInput)`
  width: ${props => props.width}px;
  padding: 0px !important;
`;

const useAutosizeInput = (inputRef, hiddenNode, value, placeholder) => {
  const [testElement, setTestElement] = React.useState<HTMLDivElement>();
  const text = value || placeholder;
  const width = React.useMemo(() => {
    if (testElement) {
      testElement.textContent = text;
      return testElement.scrollWidth;
    }

    return null;
  }, [testElement, text]);

  React.useEffect(() => {
    const node = document.createElement('div');
    const {
      fontSize,
      fontFamily,
      fontWeight,
      fontStyle,
      letterSpacing,
      textTransform,
    } = window.getComputedStyle(inputRef.current);

    node.className = hiddenNode;
    Object.assign(node.style, {
      fontSize,
      fontFamily,
      fontWeight,
      fontStyle,
      letterSpacing,
      textTransform,
    });
    if (document.body) document.body.appendChild(node);
    setTestElement(node);
    return () => {
      if (testElement && document.body) document.body.removeChild(testElement);
    };
  }, []);

  return width;
};
