import * as React from 'react';
import { createPortal } from 'react-dom';
import styled from '@emotion/styled';
import { usePopper } from 'react-popper';
import { Placement } from '@popperjs/core';

type TooltipProps = {
  elementRef: Element;
  className?: string;
  children: React.ReactNode;
  header?: React.ReactNode;
  onClick?: React.MouseEventHandler;
  placement: Placement;
  arrow?: boolean;
  tooltipAnchor?: Element;
};

const tooltipDiv = document.getElementById('__root') as HTMLDivElement;

export const Tooltip: React.FC<TooltipProps> = props => {
  const [tooltipRef, setTooltipRef] = React.useState();
  const modifiers = props.arrow
    ? [
        {
          name: 'offset',
          options: {
            offset: [0, 8],
          },
        },
      ]
    : undefined;

  const { attributes, styles } = usePopper(props.elementRef, tooltipRef, {
    strategy: 'absolute',
    placement: props.placement,
    modifiers,
  });

  const handleClick: React.MouseEventHandler = e => {
    e.preventDefault();
    e.stopPropagation();

    props.onClick && props.onClick(e);
  };

  return createPortal(
    <Root
      ref={setTooltipRef as any}
      style={styles.popper}
      {...attributes.popper}
      onClick={handleClick}
      className={props.className}
    >
      {props.header && <Header>{props.header}</Header>}
      <Content>{props.children}</Content>
      {props.arrow && (
        <PopupArrow
          data-popper-arrow
          data-placement={attributes?.popper?.['data-popper-placement']}
          {...attributes.arrow}
          style={styles.arrow}
        />
      )}
    </Root>,
    props.tooltipAnchor ?? tooltipDiv
  );
};

const PopupArrow = styled.div`
  position: absolute;
  width: 8px;
  height: 8px;
  background: inherit;

  visibility: hidden;

  &::before {
    position: absolute;
    width: 8px;
    height: 8px;
    background: inherit;

    border-style: solid;
    border-color: rgba(44, 19, 56, 0.2);

    visibility: visible;
    content: '';
    transform: rotate(45deg);
  }

  &[data-placement^='top'] {
    bottom: -4px;
    &::before {
      border-width: 0px 1px 1px 0px;
    }
  }
  &[data-placement^='bottom'] {
    top: -4px;
    &::before {
      border-width: 1px 0px 0px 1px;
    }
  }
  &[data-placement^='left'] {
    right: -4px;
    &::before {
      border-width: 1px 1px 0px 0px;
    }
  }
  &[data-placement^='right'] {
    left: -4px;
    &::before {
      border-width: 0px 0px 1px 1px;
    }
  }
`;

const Root = styled.div`
  display: flex;
  justify-content: start;
  flex-direction: column;
  max-width: 50vw;
  width: 50vw;
  transition: opacity 0.33s ease;
  position: relative;
  color: ${({ theme }) => theme.colors.ui.regular};

  font-size: 14px;
  line-height: 18px;
  text-align: center;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 2px 8px 0 rgba(44, 19, 56, 0.2);
  background-color: ${({ theme }) => theme.colors.surface_primary.regular};
  z-index: 2000;
`;

const Header = styled.span`
  color: ${({ theme }) => theme.colors.ui.regular};
  font-weight: ${({ theme }) => theme.text.semibold};
  font-size: 12px;
  text-align: left;
  margin-bottom: 10px;
`;

const Content = styled.div`
  color: ${({ theme }) => theme.colors.ui.regular};
  font-size: 12px;
  font-weight: ${({ theme }) => theme.text.normal};
  line-height: 16px;
  text-align: left;
`;
