import styled from '@emotion/styled';

import * as React from 'react';

import {
  CalendarPicker,
  useOutsideRangeReason,
  useDisableKeyboardNavigationEffect,
  useImperativeNavigationHandler,
} from '@src/packages/ui/calendars';
import moment from 'moment';

const DayPicker = React.forwardRef<
  React.Ref<{ startDayNavigation: () => void } | null>,
  React.ComponentPropsWithRef<typeof CalendarPicker> & {
    date: moment.Moment;
    withKeyboardNavigation?: boolean;
    minDate?: moment.Moment;
    maxDate?: moment.Moment;
  }
>(
  (
    { withKeyboardNavigation = true, date, maxDate, minDate, ...props },
    forwardedRef
  ) => {
    const currentDate = React.useMemo(() => moment(date), [date]);
    const [isOutsideRange, renderDayContents] = useOutsideRangeReason();
    const handleOutsideRange = React.useCallback(
      day => {
        if (maxDate && day.isAfter(maxDate, 'day')) return true;
        if (minDate && day.isBefore(minDate, 'day')) return true;

        return isOutsideRange(day);
      },
      [isOutsideRange, maxDate, minDate]
    );

    const calendarRef = React.useRef(null);

    useDisableKeyboardNavigationEffect(calendarRef, withKeyboardNavigation);
    useImperativeNavigationHandler(
      forwardedRef as React.Ref<{ startDayNavigation: () => void }>,
      calendarRef
    );

    return (
      <Content ref={calendarRef}>
        <CalendarPicker
          {...props}
          isOutsideRange={handleOutsideRange}
          renderDayContents={renderDayContents}
          date={currentDate}
        />
      </Content>
    );
  }
);

DayPicker.displayName = 'DayPicker';

const Content = styled.div`
  border-top: 1px solid ${({ theme }) => theme.colors.surface_primary.regular};
  position: relative;
  width: 100%;
  height: 360px;
`;

export default DayPicker;
